import clsx from "clsx"
import {HeadFC, PageProps} from "gatsby"
import {Fragment} from "react"
import {useLinksQuery} from "~/queries/LinksQuery"
import ValueNetworkForm from "../components/Forms/ValueNetworkForm"
import Blank from "../components/Sections/Blank/Blank"
import Carousel from "../components/Sections/Carousel/Carousel"
import HeroSection from "../components/Sections/HeroSection/HeroSection"
import Accordion from "../components/UI/Accordion/Accordion"
import BlockQuote from "../components/UI/BlockQuote/BlockQuote"
import HeroContent from "../components/UI/HeroContent/HeroContent"
import LogoContainer from "../components/UI/LogoContainer/LogoContainer"
import RichTextField from "../components/UI/RichText/RichText"
import {SEO} from "../components/UI/SEO/SEO"
import ScrollButton from "../components/UI/ScrollButton/ScrollButton"
import ScrollingText from "../components/UI/ScrollingText/ScrollingText"
import SectionContainer from "../components/UI/SectionContainer/SectionContainer"
import SlideShow from "../components/UI/SlideShow/SlideShow"
import Spacer from "../components/UI/Spacer/Spacer"
import VideoPlayer from "../components/UI/VideoPlayer/VideoPlayer"
import {PageContext} from "../types/PageContext"
import getTextFromHTML from "../utilities/getTextFromHTML"
import "./ValueNetwork.css"

type ValueNetworkPageContext = PageContext<
    Queries.ValueNetworkPageQuery["allContentfulPaginaValueNetwork"]["edges"][0]["node"]
>

interface ValueNetworkPageTemplateProps extends PageProps<any, ValueNetworkPageContext> {}

const ValueNetworkPageTemplate: React.FC<ValueNetworkPageTemplateProps> = props => {
    const valueNetworkPageData = props.pageContext.data

    return (
        <Fragment>
            <HeroSection
                id="value-network"
                image={valueNetworkPageData.heroBackgroundImage?.gatsbyImageData}
                imageAlt={valueNetworkPageData.heroBackgroundImage?.title}
            >
                <HeroContent>{valueNetworkPageData.heroContent?.raw}</HeroContent>
            </HeroSection>
            <SectionContainer tabbed marginPost customTab={<ScrollButton />} withFadeInEffect>
                <Blank>
                    <h3 className="no-margin">{valueNetworkPageData.firstParagraphTitle}</h3>
                    <RichTextField style={{marginTop: "var(--gutter-5)"}} className="font-h6 chars-h6-80">
                        {valueNetworkPageData.firstParagraphDescription?.raw}
                    </RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer>
                <Blank
                    className="black-background-dun6"
                    style={{paddingTop: "var(--gutter-7)", paddingBottom: "var(--gutter-7)"}}
                >
                    <LogoContainer images={valueNetworkPageData.subsidiariesLogos} />
                </Blank>
            </SectionContainer>
            <SectionContainer>
                <ScrollingText backgroundImage={valueNetworkPageData.scrollingTextBackgroundImage}>
                    {valueNetworkPageData.scrollingText}
                </ScrollingText>
            </SectionContainer>
            <SectionContainer>
                <Blank>
                    <RichTextField className="font-h6 chars-h6-80">
                        {valueNetworkPageData.secondParagraphDescription?.raw}
                    </RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer>
                {valueNetworkPageData.firstAccordionGroup?.map((accordion, index) => {
                    const odd = Boolean(index % 2)
                    return (
                        <Accordion
                            title={accordion?.title}
                            key={index}
                            startClosed
                            titleClassName="accordion-title-dun6"
                            iconClassName="accordion-icon-dun6"
                            className={clsx(odd ? "accordion-background-odd-90d3" : "accordion-background-even-90d3")}
                        >
                            <Blank>
                                <RichTextField className="font-h6 chars-h6-80">{accordion?.content?.raw}</RichTextField>
                            </Blank>
                        </Accordion>
                    )
                })}
            </SectionContainer>
            <SectionContainer>
                <Spacer
                    borderHeight={1}
                    backgroundImage={valueNetworkPageData.firstSpacerBackgroundImage?.gatsbyImageData}
                    backgroundImageAlt={valueNetworkPageData.firstSpacerBackgroundImage?.title}
                />
            </SectionContainer>
            <SectionContainer id="our-formats">
                <Carousel
                    title={valueNetworkPageData.formatsTitle}
                    backgroundImage={valueNetworkPageData.formatsBackgroundImage?.gatsbyImageData}
                    backgroundAlt={valueNetworkPageData.formatsBackgroundImage?.title}
                >
                    {valueNetworkPageData.ourFormats?.map((slide, index) => {
                        return (
                            <Carousel.Slide
                                key={index}
                                title={slide?.title}
                                description={slide?.description?.raw}
                                video={{
                                    src: slide?.video?.url,
                                    mimeType: slide?.video?.mimeType,
                                }}
                            />
                        )
                    })}
                </Carousel>
            </SectionContainer>
            <SectionContainer>
                <Spacer
                    borderHeight={1}
                    backgroundImage={valueNetworkPageData.secondSpacerBackgroundImage?.gatsbyImageData}
                    backgroundImageAlt={valueNetworkPageData.secondSpacerBackgroundImage?.title}
                />
            </SectionContainer>
            <SectionContainer withFadeInEffect>
                <Blank>
                    <h3 className="no-margin">
                        <div className="green">{valueNetworkPageData?.thirdParagraphTitleFirstPart}</div>
                        {valueNetworkPageData?.thirdParagraphTitleSecondPart}
                    </h3>
                    <RichTextField style={{marginTop: "var(--gutter-4)"}} className="font-h6 chars-h6-80">
                        {valueNetworkPageData?.thirdParagraphDescription?.raw}
                    </RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer>
                {valueNetworkPageData.accordionSecondGroup?.map((accordion, index) => {
                    const odd = Boolean(index % 2)
                    return (
                        <Accordion
                            title={accordion?.title}
                            key={index}
                            startClosed
                            titleClassName="accordion-title-dun6"
                            iconClassName="accordion-icon-dun6"
                            className={clsx(odd ? "accordion-background-odd-90d3" : "accordion-background-even-90d3")}
                        >
                            <Blank>
                                <RichTextField className="font-h6 chars-h6-80">{accordion?.content?.raw}</RichTextField>
                            </Blank>
                        </Accordion>
                    )
                })}
            </SectionContainer>
            <SectionContainer>
                <Spacer
                    borderHeight={1}
                    backgroundImage={valueNetworkPageData.thirdSpacerBackgroundImage?.gatsbyImageData}
                    backgroundImageAlt={valueNetworkPageData.thirdSpacerBackgroundImage?.title}
                />
            </SectionContainer>
            <SectionContainer withFadeInEffect id="our-publishers">
                <Blank>
                    <h3 className="no-margin">
                        <div className="thin-title-text-dun6 ">
                            {valueNetworkPageData?.customersSectionTitleFirstPart}
                        </div>
                        <span className="green">{valueNetworkPageData?.customersSectionTitleSecondPart}</span>
                    </h3>
                    <RichTextField style={{marginTop: "var(--gutter-4)"}} className="font-h6 chars-h6-80">
                        {valueNetworkPageData?.customerSectionDescription?.raw}
                    </RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer title={valueNetworkPageData.customersLogosTitle}>
                <Blank className="customers-section-dun6" style={{paddingTop: 0}}>
                    <LogoContainer
                        images={valueNetworkPageData.customersSubsidiariesLogos}
                        style={{flexWrap: "nowrap"}}
                        className="customers-sections-ubsidiaries-logos-dun6"
                    />
                    <LogoContainer
                        images={valueNetworkPageData.customersLogos}
                        style={{
                            paddingTop: "var(--gutter-5)",
                            rowGap: "var(--gutter-0)",
                            columnGap: "var(--gutter-4)",
                            justifyContent: "space-around",
                        }}
                    />
                </Blank>
            </SectionContainer>
            <SectionContainer>
                <Spacer
                    borderHeight={1}
                    backgroundImage={valueNetworkPageData.fifthSpacerBackgroundImage?.gatsbyImageData}
                    backgroundImageAlt={valueNetworkPageData.fifthSpacerBackgroundImage?.title}
                />
            </SectionContainer>
            <SectionContainer>
                <Blank>
                    <h3 className="no-margin">
                        <span className="green">{`${valueNetworkPageData?.formTitleFirstPart} `}</span>
                        <span className="white-space-no-wrap-dun6">{valueNetworkPageData?.formTitleSecondPart}</span>
                    </h3>
                </Blank>
                <ValueNetworkForm labels={valueNetworkPageData} />
            </SectionContainer>
            <SectionContainer>
                <Spacer
                    borderHeight={1}
                    backgroundImage={valueNetworkPageData.fourthSpacerBackgroundImage?.gatsbyImageData}
                    backgroundImageAlt={valueNetworkPageData.fourthSpacerBackgroundImage?.title}
                />
            </SectionContainer>
            <SectionContainer id="our-last-projects">
                <VideoPlayer
                    title={valueNetworkPageData.promoVideo?.title}
                    description={valueNetworkPageData.promoVideo?.description?.raw}
                    backgroundImage={valueNetworkPageData.promoVideo?.image?.gatsbyImageData}
                    backgroundAlt={valueNetworkPageData.promoVideo?.image?.title}
                    videoSources={{
                        src: valueNetworkPageData.promoVideo?.video?.url,
                        mimeType: valueNetworkPageData.promoVideo?.video?.mimeType,
                    }}
                />
            </SectionContainer>
            <SectionContainer id="testimonials">
                <Blank>
                    <h3>{valueNetworkPageData?.testimonialsTitle}</h3>
                    <SlideShow>
                        {valueNetworkPageData.testimonials?.map((t, i) => (
                            <BlockQuote key={i} author={t?.author ?? ""}>
                                {t?.content?.raw}
                            </BlockQuote>
                        ))}
                    </SlideShow>
                </Blank>
            </SectionContainer>
        </Fragment>
    )
}

export default ValueNetworkPageTemplate

export const Head: HeadFC<any, ValueNetworkPageContext> = props => {
    const valueNetworkPageData = props.pageContext.data

    const links = useLinksQuery()

    return (
        <SEO
            title={valueNetworkPageData?.title}
            description={getTextFromHTML(valueNetworkPageData?.firstParagraphDescription?.raw)}
            language={props.pageContext.language}
            image={valueNetworkPageData.seoImage?.url}
            imageAlt={valueNetworkPageData?.seoImage?.title}
            siteUrl={`${process.env.SITE_URL}${links.valueNetwork.url}`}
        />
    )
}
