import {clsx} from "clsx"
import {GatsbyImage} from "gatsby-plugin-image"
import {FC, HTMLAttributes} from "react"
import "./LogoContainer.css"

type LogoContainerProps = HTMLAttributes<HTMLDivElement> & {
    images:
        | ReadonlyArray<{
              readonly width: number | null | undefined
              readonly url: string | null | undefined
              readonly title: string | null | undefined
              readonly height: number | null | undefined
              readonly gatsbyImageData: import("gatsby-plugin-image").IGatsbyImageData | null | undefined
          } | null>
        | null
        | undefined
}

const LogoContainer: FC<LogoContainerProps> = props => {
    const {className, children, images, ...otherProps} = props

    return (
        <div className={clsx("logo-container-3js8", className)} {...otherProps}>
            {images &&
                images.map((logo, index) => {
                    if (!logo?.url || !logo.gatsbyImageData) return
                    return (
                        <div className="image-container-3js8" key={index}>
                            <GatsbyImage image={logo.gatsbyImageData} alt={logo.title ?? ""} key={index} />
                        </div>
                    )
                })}
        </div>
    )
}

export default LogoContainer
