import clsx from "clsx"
import {useState} from "react"
import {SubmitHandler, useForm} from "react-hook-form"
import Button from "~/components/UI/Button/Button"
import InputText from "~/components/UI/InputText/InputText"
import CheckIcon from "../../icons/CheckIcon"
import ErrorIcon from "../../icons/ErrorIcon"
import {emailRegex, nameRegex, numberRegex, urlRegex} from "../../utilities/RegexStrings"
import "./Form.css"

type ValueNetworkLabels = {
    readonly formEmailLabel: string | null
    readonly formFieldsLabel: string | null
    readonly formLastNameLabel: string | null
    readonly formNameLabel: string | null
    readonly formSessionsLabel: string | null
    readonly formSubmitButton: string | null
    readonly formSubmitSuccessMessage: string | null
    readonly formUrlLabel: string | null
    readonly formValueLabel: string | null
    readonly formViewsLabel: string | null
}

interface ValueNetworkFormProps extends React.FormHTMLAttributes<HTMLFormElement> {
    labels: ValueNetworkLabels
}

type ValueNetworkFields = {
    Website: string
    Number: string
    Number1: string
    Name_First: string
    Name_Last: string
    Email: string
}

type SubmitStatus = undefined | "success" | "error"

const ValueNetworkForm: React.FC<ValueNetworkFormProps> = props => {
    const {className, children, labels, ...otherProps} = props
    const [submitStatus, setSubmitStatus] = useState<SubmitStatus>()

    const {
        register,
        handleSubmit,
        watch,
        formState: {errors},
    } = useForm<ValueNetworkFields>()

    const onFormSubmitHandler: SubmitHandler<ValueNetworkFields> = async data => {
        const url =
            "https://forms.zohopublic.eu/adkaora/form/calcolatore/formperma/DEkOD-r6j5npz_Yzfh5jL5UebikqWJD6H3Px-yWIPJQ/htmlRecords/submit"
        const formData = new FormData()
        for (const field in data) {
            type ObjectKey = keyof ValueNetworkFields
            const fieldKey = field as ObjectKey
            formData.append(field, data[fieldKey])
        }
        const requestOptions: RequestInit = {
            method: "POST",
            mode: "cors",
            body: formData,
            redirect: "manual",
            cache: "no-cache",
            credentials: "same-origin",
        }
        await fetch(url, requestOptions)
            .then(result => {})
            .catch((error: Error) => {})
            .finally(() => {
                setSubmitStatus("success")
            })
    }

    return (
        <form
            className={clsx("white-background-ykf9", className)}
            style={{position: "relative"}}
            onSubmit={handleSubmit(onFormSubmitHandler)}
            {...otherProps}
        >
            <div className={clsx("form-ykf9", {"hidden-ykf9": submitStatus === "success"})}>
                <InputText
                    type="text"
                    placeholder={labels.formUrlLabel}
                    invalid={errors.Website !== undefined}
                    {...register("Website", {required: true, pattern: urlRegex})}
                    variant="soft-filled"
                    containerClassName="span2-ykf9"
                />
                <InputText
                    type="text"
                    placeholder={labels.formViewsLabel}
                    invalid={errors.Number !== undefined}
                    {...register("Number", {required: true, pattern: numberRegex})}
                    variant="soft"
                    containerClassName="span2-ykf9"
                />
                <InputText
                    type="text"
                    placeholder={labels.formSessionsLabel}
                    invalid={errors.Number1 !== undefined}
                    {...register("Number1", {required: true, pattern: numberRegex})}
                    variant="soft-filled"
                    containerClassName="span2-ykf9"
                />
                <InputText
                    type="text"
                    placeholder={labels.formNameLabel}
                    invalid={errors.Name_First !== undefined}
                    {...register("Name_First", {required: true, pattern: nameRegex})}
                    variant="soft"
                    containerClassName="span2-ykf9"
                />
                <InputText
                    type="text"
                    placeholder={labels.formLastNameLabel}
                    invalid={errors.Name_Last !== undefined}
                    {...register("Name_Last", {required: true, pattern: nameRegex})}
                    variant="soft-filled"
                    containerClassName="span2-ykf9"
                />
                <InputText
                    type="email"
                    placeholder={labels.formEmailLabel}
                    invalid={errors.Email !== undefined}
                    {...register("Email", {
                        required: true,
                        pattern: emailRegex,
                    })}
                    variant="soft"
                    containerClassName="span2-ykf9"
                />
                <div className="span2-ykf9 button-ykf9">
                    <Button type="submit">{labels.formSubmitButton}</Button>
                </div>
            </div>
            {submitStatus === "success" && (
                <div className="overlay-ykf9">
                    <div className="modal-ykf9">
                        <CheckIcon className="check-icon-ykf9" />
                        <span className="submit-success-message-ykf9">{labels.formSubmitSuccessMessage}</span>
                    </div>
                </div>
            )}
            {submitStatus === "error" && (
                <div className="overlay-ykf9">
                    <div className="modal-ykf9">
                        <ErrorIcon className="error-icon-ykf9" />
                        <span className="submit-success-message-ykf9">Error</span>
                    </div>
                </div>
            )}
        </form>
    )
}

export default ValueNetworkForm
