import clsx from "clsx"
import {BlockquoteHTMLAttributes, FC} from "react"
import QuoteIcon from "../../../icons/QuoteIcon"
import RichTextField from "../RichText/RichText"
import "./BlockQuote.css"

type BlockQuoteProps = BlockquoteHTMLAttributes<HTMLQuoteElement> & {
    author: string
}

const BlockQuote: FC<BlockQuoteProps> = props => {
    const {children, className, author, ...otherProps} = props
    return (
        <blockquote className={clsx("blockquote-k04l", className)} {...otherProps}>
            <QuoteIcon className="quote-icon" />
            <RichTextField className="content chars-h6-80">{children}</RichTextField>
            <footer className="author">{author}</footer>
        </blockquote>
    )
}

export default BlockQuote
