import clsx from "clsx"
import {Children, FC, HTMLAttributes, Reducer, useCallback, useEffect, useReducer, useRef} from "react"
import ChevronRightIcon from "../../../icons/ChevroRightIcon"
import ChevronLeftIcon from "../../../icons/ChevronLeftIcon"
import {onTrasitionEnd} from "../../../utilities/transition"
import DotsSelect from "../DotsSelect/DotsSelect"
import "./SlideShow.css"

type SlideShowProps = HTMLAttributes<HTMLDivElement> & {}

type SlideStateClassnameType = null | "prev" | "next"

type SlideshowState = {
    numberOfItems: number
    currentIndex: number
    targetIndex: number
    isTransitionEnabled: boolean
    isReadyToAnimate: boolean
    prevClassname: SlideStateClassnameType
    currentClassname: SlideStateClassnameType
    nextClassname: SlideStateClassnameType
}

const slideshowInitialState: SlideshowState = {
    numberOfItems: 0,
    currentIndex: 0,
    targetIndex: 0,
    isTransitionEnabled: true,
    isReadyToAnimate: true,
    prevClassname: "prev",
    currentClassname: null,
    nextClassname: "next",
}

type SlideshowActionType =
    | "transit-forward"
    | "transit-backward"
    | "enable-controls"
    | "disable-controls"
    | "enable-transitions"
    | "disable-transitions"
    | "reset-transition"

type SlideshowActionWithPayloadType = "go-to" | "update-current-index"

type SlideshowAction = {type: SlideshowActionType}
type SlideshowActionWithPayload = {type: SlideshowActionWithPayloadType; value: number}

const mod = (value: number, module: number) => ((value % module) + module) % module

const slideshowReducer: Reducer<SlideshowState, SlideshowAction | SlideshowActionWithPayload> = (state, action) => {
    switch (action.type) {
        case "enable-controls":
            return {...state, isReadyToAnimate: true}
        case "disable-controls":
            return {...state, isReadyToAnimate: false}
        case "enable-transitions":
            return {...state, isTransitionEnabled: true}
        case "disable-transitions":
            return {...state, isTransitionEnabled: false}
        case "reset-transition":
            return {...state, currentClassname: null, prevClassname: "prev", nextClassname: "next"}
        case "transit-forward":
            return {...state, currentClassname: "prev", nextClassname: null}
        case "transit-backward":
            return {...state, currentClassname: "next", prevClassname: null}
        case "update-current-index":
            return {
                ...state,
                currentIndex: mod(action.value, state.numberOfItems),
            }
        case "go-to":
            return {...state, targetIndex: mod(action.value, state.numberOfItems)}
        default:
            return {...state}
    }
}

type CoordinatesType = {x: number | undefined; y: number | undefined}

const SlideShow: FC<SlideShowProps> = props => {
    const {children, className, ...otherProps} = props
    const items = Children.toArray(children)
    const [state, dispatch] = useReducer(slideshowReducer, {...slideshowInitialState, numberOfItems: items.length})
    const currentSlideRef = useRef<HTMLDivElement>(null)
    const dragStartingPosRef = useRef<CoordinatesType | null>(null)

    const handleDragStart = useCallback(
        (event: React.TouchEvent<HTMLDivElement>) => {
            dragStartingPosRef.current = {x: event.touches[0]?.clientX, y: event.touches[0]?.clientY}
        },
        [dragStartingPosRef.current],
    )

    const handleDrag = useCallback(
        (event: React.TouchEvent<HTMLDivElement>) => {
            if (!dragStartingPosRef.current || !dragStartingPosRef.current.x || !state.isReadyToAnimate) return
            const dragCurrentPos = {x: event.touches[0]?.clientX, y: event.touches[0]?.clientY}
            if (!dragCurrentPos.x) return
            const diff = dragCurrentPos.x - dragStartingPosRef.current.x
            if (Math.abs(diff) < 10) return

            if (diff < 0) {
                dispatch({type: "go-to", value: state.currentIndex + 1})
            } else {
                dispatch({type: "go-to", value: state.currentIndex - 1})
            }
        },
        [dragStartingPosRef.current?.x, state.isReadyToAnimate],
    )

    const handleNextClick = () => {
        dispatch({type: "go-to", value: state.currentIndex + 1})
    }

    const handlePrevClick = () => {
        dispatch({type: "go-to", value: state.currentIndex - 1})
    }

    const goToNext = useCallback(() => {
        if (!state.isReadyToAnimate) return
        onTrasitionEnd(currentSlideRef.current, () => {
            dispatch({type: "disable-transitions"})
            dispatch({type: "reset-transition"})
            dispatch({type: "update-current-index", value: state.currentIndex + 1})
            setTimeout(() => {
                dispatch({type: "enable-transitions"})
                dispatch({type: "enable-controls"})
            }, 10)
        })
        dispatch({type: "disable-controls"})
        dispatch({type: "transit-forward"})
    }, [state.isReadyToAnimate, currentSlideRef.current])

    const goToPrev = useCallback(() => {
        if (!state.isReadyToAnimate) return
        onTrasitionEnd(currentSlideRef.current, () => {
            dispatch({type: "disable-transitions"})
            dispatch({type: "reset-transition"})
            dispatch({type: "update-current-index", value: state.currentIndex - 1})
            setTimeout(() => {
                dispatch({type: "enable-transitions"})
                dispatch({type: "enable-controls"})
            }, 10)
        })
        dispatch({type: "transit-backward"})
        dispatch({type: "disable-controls"})
    }, [state.isReadyToAnimate, currentSlideRef.current])

    const findFastestWay = (start: number, end: number, total: number): "forward" | "backward" => {
        const tStart = start ?? total
        const tEnd = end ?? total

        const diffPos = (tEnd - tStart + total) % total
        const diffNeg = (tEnd - tStart - total) % total

        if (Math.abs(diffNeg) < diffPos) return "backward"
        if (Math.abs(diffNeg) > diffPos) return "forward"

        if (end < start) {
            return "backward"
        } else {
            return "forward"
        }
    }

    const handleDotClick = (desiredIndex: number) => {
        dispatch({type: "go-to", value: desiredIndex})
    }

    useEffect(() => {
        if (state.currentIndex === state.targetIndex) return

        const direction = findFastestWay(state.currentIndex, state.targetIndex, state.numberOfItems)

        if (direction === "forward") {
            goToNext()
        } else {
            goToPrev()
        }
    }, [state])

    return (
        <div
            className={clsx("slideshow-73jc", {transition: state.isTransitionEnabled}, className)}
            onTouchStart={handleDragStart}
            onTouchMove={handleDrag}
            {...otherProps}
        >
            <div className={clsx("slide", state.prevClassname)}>
                {items[mod(state.currentIndex - 1, state.numberOfItems)]}
            </div>
            <div className={clsx("slide", state.currentClassname)} ref={currentSlideRef}>
                {items[state.currentIndex]}
            </div>
            <div className={clsx("slide", state.nextClassname)}>
                {items[mod(state.currentIndex + 1, state.numberOfItems)]}
            </div>
            <button className="prev-button" onClick={handlePrevClick} disabled={!state.isReadyToAnimate}>
                <ChevronLeftIcon className="icon" />
            </button>
            <button className="next-button" onClick={handleNextClick} disabled={!state.isReadyToAnimate}>
                <ChevronRightIcon className="icon" />
            </button>
            <DotsSelect
                className="dots"
                length={state.numberOfItems}
                selected={state.targetIndex}
                onSelect={handleDotClick}
            />
        </div>
    )
}

export default SlideShow
