import clsx from "clsx"
import {GatsbyImage} from "gatsby-plugin-image"
import {FC, HTMLAttributes, useRef} from "react"
import "./ScrollingText.css"

type ScrollingTextProps = Omit<HTMLAttributes<HTMLDivElement>, "children"> & {
    backgroundImage: {
        readonly width: number | null
        readonly url: string | null
        readonly title: string | null
        readonly height: number | null
        readonly gatsbyImageData: import("gatsby-plugin-image").IGatsbyImageData | null
    } | null
    children: string | undefined | null
}

const ScrollingText: FC<ScrollingTextProps> = props => {
    const {children, className, backgroundImage, ...otherProps} = props
    const containerRef = useRef<HTMLDivElement>(null)

    const animationDuration = children ? `${children.length * 0.5}s` : "10s"

    return (
        <div className={clsx("scrolling-text-r01z", className)} {...otherProps}>
            {backgroundImage && backgroundImage.gatsbyImageData && (
                <GatsbyImage
                    className="gatsby-image-r01z"
                    image={backgroundImage.gatsbyImageData}
                    alt={backgroundImage.title ?? ""}
                />
            )}

            {/* con 7 elementi, fino a 10 caratteri supportati */}
            <div className="scroll-container-r01z" ref={containerRef}>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
                <span className="text-r01z" style={{animationDuration}}>
                    {children}
                </span>
            </div>
        </div>
    )
}

export default ScrollingText
