import {documentToHtmlString} from "@contentful/rich-text-html-renderer"
import clsx from "clsx"
import {FC, HTMLAttributes} from "react"
import "./HeroContent.css"

type HeroContentProps = HTMLAttributes<HTMLDivElement> & {}

const HeroContent: FC<HeroContentProps> = props => {
    const {className, children, ...otherProps} = props
    return (
        <div
            className={clsx("hero-content-z9p0", className)}
            dangerouslySetInnerHTML={
                children ? {__html: documentToHtmlString(JSON.parse(String(children)))} : undefined
            }
            {...otherProps}
        />
    )
}

export default HeroContent
